
body,a,p,li,ul,h1,h2,h3,h4,h5,strong,div,img{
  font-size: 0.12rem; font-family: "微软雅黑",arial; margin: 0; padding: 0; border: none; text-decoration: none;
}
ul,li{
  list-style:none;
}
#app {
    height: 100%;
}
body {
    height: 100%;
}
html {
    height: 100%;
}
.wrap{
	overflow-x: hidden;
}
.nav{
	position:absolute; z-index:99; top:0.62rem; left: 0; width: 100%;
}
.nav a{
	display:block; height:0.42rem; border-bottom: 0.01rem solid #fff; line-height:0.42rem; padding-left: 0.2rem; color:rgba(255,255,255,1);
}
.nav .router-link-exact-active{
	color:#fff; background:#036193;
}
.layout{
   width: 2.3rem; padding-top: 0.1rem; overflow: hidden;
}
.layout span{
	font-size: 0.14rem; display: block; color: rgba(255,255,255,.9); text-align: center; line-height: 0.5rem;
}
.vux-drawer .actives{
  -webkit-transform:translate3d(2.3rem, 0px, 0px) !important; transform:translate3d(2.3rem, 0px, 0px) !important;
}
.vux-drawer .activer{
  -webkit-transform:translate3d(-2.3rem, 0px, 0px) !important; transform:translate3d(-2.3rem, 0px, 0px) !important;
}
.vux-drawer > .vux-drawer-content{
	 background:#0086cd;
}
.head{
	height:0.65rem; overflow: hidden; width: 100%; color:#49b69a;
}
.head p{
	font-size:0.18rem; font-weight: bold; margin-top:0.1rem;
}
.head .logo{
	float:left; width:82%; padding:0.12rem 0 0 0.12rem; margin-bottom:0.12rem;
}
.head .logo strong{
	width:0.44rem; height:0.44rem; font-size:0.15rem; float:left; margin-right:0.1rem; margin-top:0.12rem;
}
.head .logo img{
	width:0.44rem; height:0.44rem; font-size:0.15rem; float:left; margin-right:0.1rem;
}
.head .icon{
	float:left; width:0.4rem; height:0.4rem; border-radius: 0.04rem; margin-top:0.1rem; background:#49b69a;
}
.head .icon svg{
	fill:#fff; margin:0.05rem 0; width:0.4rem; height:0.4rem; cursor:pointer; height: 0.3rem;
}
.title{
    text-align:center; padding:0.2rem 0; clear: both;
}
.title a{
	color:#000; font-size:0.18rem; display:block; line-height:0.3rem;
}
.title span{
	color:rgb(204, 204, 204);
}
.page{
    margin-bottom:0.2rem;color:#5a5a5a; overflow:hidden; text-align:center; clear: both; padding: 0.1rem 0;
}
.page .num{
    border:0.01rem solid #EEE; margin:0 0.015rem; padding:0 0.05rem;
}
.page a{ 
    color:#999;
}
.page .active{
    color:#fff; font-weight:bold; border:0.01rem solid #000; background:#000;
}
.page a:hover{
    color:#f55e6a; border:0.01rem solid #000; background-color:#000;
}
.page .prev{
    border:0.01rem solid #EEE; color:#ddd;
}
  
/* 路由切换动画 */
.child-view {  
  width: 100%; height: 100%; -webkit-transition: all 0.5s cubic-bezier(.55,0,.1,1);  -webkit-transition: all .5s cubic-bezier(.55,0,.1,1);  transition: all .5s cubic-bezier(.55,0,.1,1);
}
.vux-pop-out-enter-active,
.vux-pop-out-leave-active,
.vux-pop-in-enter-active,
.vux-pop-in-leave-active {
  will-change: transform; -webkit-transition: all 0.5s; transition: all 0.5s; height: 100%; position: absolute;  -webkit-backface-visibility: hidden;  backface-visibility: hidden;  -webkit-perspective: 1000;  perspective: 1000;
}
.vux-pop-out-enter {
  opacity: 0; -webkit-transform: translate3d(-100%, 0, 0); transform: translate3d(-100%, 0, 0);
}
.vux-pop-out-leave-active {
  opacity: 0; -webkit-transform: translate3d(100%, 0, 0); transform: translate3d(100%, 0, 0);
}
.vux-pop-in-enter {
  opacity: 0; -webkit-transform: translate3d(100%, 0, 0); transform: translate3d(100%, 0, 0);
}
.vux-pop-in-leave-active {
  opacity: 0; -webkit-transform: translate3d(-100%, 0, 0); transform: translate3d(-100%, 0, 0);
}



